@import url(https://fonts.googleapis.com/css2?family=Lora&family=Montserrat&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* @import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap'); */
 
:root {
  --theme-col-1: hsl(199, 78%, 42%);
  --theme-col-2: hsl(0,0%,0%);
  font-size: 16px;
}

.App {
  display:grid;
  grid-template-areas: 
  'header header header'
  'main main main'
  'footer footer footer';
  place-content: center;
  place-items: center;
  font-family: 'Roboto', sans-serif;
}

.title, h1, h2, header {
  font-family: 'Lora', serif;
}

.App > header {
  grid-area: header;
  display:grid;
  grid-auto-flow: column;
  font-size: 30px;
  grid-template-areas: 
  "logo logo"
  "navToggle donate";
  place-items: center;
  place-content: center;
  grid-gap:2vh 20vw;
  gap:2vh 20vw;
  width:100%;
  padding-bottom: 1vh;
  background-color: white;
  transition: all 0.3s;
}

header > nav {
  display:grid;
  padding:2vh 2vw;
  grid-auto-flow: row;
  grid-gap:2ch;
  gap:2ch;
  place-content: center;
  place-items: center;
  position: fixed;
  top:0;
  grid-area: nav;
  background-color: hsl(199, 78%, 42%);
  background-color: var(--theme-col-1);
  color:white;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
  height:100vh;
  box-shadow: 1px 0 2px #222;
}

header > nav > a {
  display: grid;
  place-content: center;
  width:100%;
  height:5vh;
  text-align: center;
  /* border: 2px solid black;
  border-radius: 5px; */
}

#logo {
  grid-area: logo;
  height: 15vh;
}


#nav-toggler {
  grid-area: navToggle;
  display: flex;
  place-content: center;
  place-items: center;
  flex-direction: column;
  font-size: 10vh;
}
#nav-toggler div {
  height:3px;
  background-color: black;
  width:25px;
  margin:3px;
  transition: transform 0.3s;
}

#nav-toggler.toggled div:first-child {
  transform: translateX(0px) translateY(9px) rotate(-45deg)
}
#nav-toggler.toggled div:nth-child(2) {
  transform: scale(0)
}
#nav-toggler.toggled div:nth-child(3) {
  transform: translateX(0px) translateY(-9px) rotate(45deg)
}

header > nav.toggled {
  transform: translateX(0);
}

header > nav > a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s;
}

header > nav > a:hover {
  color:hsl(199, 78%, 42%);
  color:var(--theme-col-1);
}
header > nav > a.nav-active {
  color:black;
}

.donate-wrapper {
  display: grid;
  place-items: center;
  grid-area:donate

}

#donate {
  font-size: inherit;
  border:none;
  border-radius: 10px;
  padding:1vh 2vw;
  background-color: hsl(199, 78%, 42%);
  background-color: var(--theme-col-1);
  color:#fff;
  cursor: pointer;
  box-shadow: 0 1px 2px black;
}

#donate::after {
  content:"donate"
}

#donate:hover {
  background-color: hsl(0,0%,0%);
  background-color: var(--theme-col-2);
}

.App > main {
  grid-area: main;
  min-width: 80vw;
  min-height: 80vh;
  width:100vw;
  margin-bottom: 10vh;
}

.App > footer {
  grid-area: footer;
  height:7vh;
  display: grid;
  grid-auto-flow: column;
  place-items: center;
  color:white;
  background-color: #222;
  width:100%;
}

.App > footer a {
  color:inherit;
}

#loader {
  position: fixed;
  top:calc(50vh - 2.5vmin);
  left:calc(50vw - 7.5vmin);
  display: none;
  grid-auto-flow: column;
  grid-gap:1vmin;
  gap:1vmin;
}
#loader.visible {
  display: grid;
}
#loader div {
  border-radius: 100%;
  width:5vmin;
  height:5vmin;
  background-color:hsl(199, 78%, 42%);
  background-color:var(--theme-col-1);
  animation: rescale 1.3s linear infinite;
  transform: scale(0.5);
}

#loader div:nth-child(1) {
  animation-delay: 0s;
}
#loader div:nth-child(2) {
  animation-delay: 0.3s;
}
#loader div:nth-child(3) {
  animation-delay: 0.6s;
}

@media screen and (min-width: 768px){
  .App > header {
    grid-template-areas: "logo nav nav donate";
    position: -webkit-sticky;
    position: sticky;
    grid-gap:2ch;
    gap:2ch;
    top:0px;
  }
  .App > header.isSticky {
    grid-template-areas: "nav donate";
    background-color: #222;
    color:white;
    padding-top: 1vh;
    box-shadow: 0 2px 1px black;
  }
  .App > header.isSticky > #logo {
    display: none;
  }

  .App > header .nav-active {
    color:hsl(199, 78%, 42%);
    color:var(--theme-col-1);
  }
  .App > header.isSticky .nav-active {
    color:hsl(199, 78%, 42%);
    color:var(--theme-col-1);
  }
  header > nav {
    display:grid;
    grid-auto-flow: column;
    color:inherit;
    background: none;
    position: unset;
    height:auto;
    box-shadow: none;
    padding: 0;
    grid-gap:2vw;
    gap:2vw;
    transform: translateX(0);
  }

  #nav-toggler {
    display:none;
    position: relative;
  }

  .App > main {
    width:80vw;
    display:grid;
    place-items: center;
  }
  p {
    width:80%;
  }
}

@keyframes rescale {
  from {
    transform: scale(0.5);
  }
  25% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(0.5);
  }
}
.title-wrapper {
  display: grid;
  width:100%;
  height:50vw;
  place-items: center;
  font-size: 10vmin;
  background-size: cover;
  background-attachment: fixed;
  height: 40vh;
  color:white;
  margin-bottom: 2vh;
  background-color: hsla(0,0%,0%,0.5);
}

p {
  padding:0vh 2vw;
}

#values-we-follow {
  display: grid;
  align-items: flex-start;
  justify-items:center;
  align-content: center;
  justify-content: center;
  grid-gap:4vh 1vw;
  gap:4vh 1vw;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

#values-we-follow li {
  list-style-type: none;
  display: block;
  border: 2px solid var(--theme-col-1);
  border-radius: 5px;
  padding:2vh 2vw;
  max-width: 90vw;
  height: 90%;
  overflow-y: auto;
}

#board {
  display: grid;
  align-items: flex-start;
  justify-items:center;
  align-content: center;
  justify-content: center;
  grid-gap:4vh 1vw;
  gap:4vh 1vw;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

#board li {
  list-style-type: none;
  display: block;
  border: 2px solid var(--theme-col-1);
  border-radius: 5px;
  padding:2vh 2vw;
  max-width: 90vw;
  height: 100%;
  overflow-y: auto;
}

@media screen and (min-width: 768px) {
  .title-wrapper {
    height:70vh;
  }
  #values-we-follow {
    grid-auto-flow: column;
  }
  #board {
    grid-auto-flow: column;
  }
}
